import React, { Component } from 'react';
import './ShoppingCart.css';
import productImg from '../../public/img/product-1.jpg';

class ShoppingCart extends Component {
  render() {
    return (
      <div className="page shoppingCart">
        <div className="block item">
          <img src={productImg} alt="productimg"></img>
          <div className="description">
            <div className="column">
              <label className="title">商品標題:</label>
              <p className="content">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Deserunt voluptates rerum eveniet sapiente repellat esse, doloremque quod recusandae deleniti nostrum assumenda vel beatae sed aut modi nesciunt porro quisquam voluptatem.</p>
            </div>
            <div className="column">
              <label className="title">尺寸:</label>
              <p className="content">L</p>
            </div>
            <div className="column">
              <label className="title">顏色:</label>
              <p className="content">紅</p>
            </div>
            <div className="btn btn-danger">刪除</div>
          </div>
        </div>
        <div className="block item">
          <img src={productImg} alt="productimg"></img>
          <div className="description">
            <div className="column">
              <label className="title">商品標題:</label>
              <p className="content">Lorem ipsum dolor</p>
            </div>
            <div className="column">
              <label className="title">尺寸:</label>
              <p className="content">L</p>
            </div>
            <div className="column">
              <label className="title">顏色:</label>
              <p className="content">紅</p>
            </div>
            <div className="btn btn-danger">刪除</div>
          </div>
        </div>
        <div className="block item">
          <img src={productImg} alt="productimg"></img>
          <div className="description">
            <div className="column">
              <label className="title">商品標題:</label>
              <p className="content title">Lorem ipsum dolor </p>
            </div>
            <div className="column">
              <label className="title">尺寸:</label>
              <p className="content size">L</p>
            </div>
            <div className="column">
              <label className="title">顏色:</label>
              <p className="content color">紅</p>
            </div>
            <div className="btn btn-danger">刪除</div>
          </div>
        </div>
        <div className="btnBlock">
          <div className="btn checkout">結帳</div>
        </div>
      </div>
    );
  }
}

export default ShoppingCart;
