import React, { Component } from 'react';
import './ProductDetail.css';
import productImg from '../../public/img/product-1.jpg';
import { FormGroup, ControlLabel, FormControl } from "react-bootstrap";

class ProductDetail extends Component {
  render() {
    return (
      <div className="page ProductDetail">
        <div className=" block titleBlock">
          <div className="imgBlock">
            <img src={productImg}></img>
          </div>
          <div className="description">
          <div className="column">
              <label className="title">商品標題:</label>
              <p className="content">Lorem ipsum dolor</p>
            </div>
            <div className="column">
              <label className="title">尺寸:</label>
              <p className="content">L</p>
            </div>
            <div className="column">
              <label className="title">顏色:</label>
              <p className="content">紅</p>
            </div>
            <FormGroup className="column" controlId="formControlsSelect">
              <ControlLabel className="title">Select</ControlLabel>
              <FormControl className="content quantity" componentClass="select">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </FormControl>
            </FormGroup>
            <div className="btnBlock">
              <div className="btn putInCart">加入購物車</div>
              <div className="btn checkout">結帳</div>
            </div>
          </div>
        </div>
        <div className="block contentBlock">
          <p>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Deserunt voluptates rerum eveniet sapiente repellat esse, doloremque quod recusandae deleniti nostrum assumenda vel beatae sed aut modi nesciunt porro quisquam voluptatem.
          </p>
          <img src={productImg}></img>
          <img src={productImg}></img>
          <img src={productImg}></img>
          
        </div>
      </div>
    );
  }
}

export default ProductDetail;
