import React, { Component } from 'react';
import './Nav.css';
// import { Navbar, NavItem } from "react-bootstrap";
import { Navbar, NavItem, Nav as NavBlock } from "react-bootstrap";
import { HashRouter as Router, Route, Link } from 'react-router-dom';

class Nav extends Component {
  render() {
    return (
      <Router>
        <Navbar>
          <Navbar.Header>
            <Navbar.Brand>
              <Link className="navbar-brand" to="/">Normal</Link>
            </Navbar.Brand>
          </Navbar.Header>
          <NavBlock>
            <NavItem componentClass={Link} href="/" to="/productList">
              商品列表
            </NavItem>
            <NavItem componentClass={Link} href="/" to="/shoppingCart">
              購物車
            </NavItem>
            <NavItem componentClass={Link} href="/" to="/productdetail">
              tmp商品明細
            </NavItem>
          </NavBlock>
        </Navbar>
      </Router>
    );
  }
}

export default Nav;
